.popup__save-button {
  margin: 31px 36px 37px;
  background-color: black;
  color: white;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  line-height: 22px;
  border: transparent;
  width: 83.27%;
  cursor: pointer;
  border-radius: 2px;
  padding: 0;
}

.popup__save-button:hover {
  background-color: rgba(0, 0, 0, 0.8);
}

@media (max-width: 505px) {
  .popup__save-button {
    margin: 45px 22px 25px;
    width: 238px;
    font-size: 14px;
    line-height: 17px;
  }
}

.popup__save-button:disabled {
  background-color: white;
  opacity: 0.2;
  border: 1px solid black;
  color: black;
  pointer-events: none;
}

