.profile__name {
  font-weight: 500;
  font-size: 42px;
  line-height: 48px;
  color: #FFFFFF;
  margin: 0;
  grid-area: profile__name;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  max-width: 300px;
}

@media (max-width: 450px) {
  .profile__name {
    font-size: 27px;
    line-height: 33px;
    max-width: 250px;
  }
}
