.profile__profession {
  font-weight: normal;
  font-size: 18px;
  line-height: 22px;
  color: #FFFFFF;
  margin: 0;
  grid-area: profile__profession;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  max-width: 300px;
}

@media (max-width: 983px) {
  .profile__profession {
    text-align: center;
  }
}

@media (max-width: 450px) {
  .profile__profession {
    font-size: 14px;
    line-height: 17px;
    max-width: 250px;
  }
}
